html {
  font-size: 10px;
}

body {
  --keyboard-bg-color: #bbb;
  --key-bg-color: #eee;
  --key-color: #808080;
  --lock-on-color: #333;
  min-height: 100vh;
  overflow: hidden;
  margin: 0;
  padding: 0;
}
main {
  display: flex;
  flex-direction: column;
  justify-content: stretch;
  min-height: 100vh;
}
.material-icons {
  font-size: 2.4rem;
}
.output {
  width: 90%;
  margin: 0 auto auto auto;
  flex: 100%;
}
.output:focus {
  outline: none;
}
.keyboard {
  max-width: 125rem;
  width: 90%;
  margin: 2rem auto;
  background-color: var(--keyboard-bg-color);
  padding: 2rem 1rem;
  border-radius: 1.5rem;
  justify-content: center;
  box-shadow: 0 2.7rem 2.4rem 0 rgba(0, 0, 0, 0.2), 0 4rem 7.7rem 0 rgba(0, 0, 0, 0.22);
  transition: transform 1s;
  z-index: 20;
}

.keyboard.hide {
  transform: translateY(200%);
}
.keyboard__row {
  display: flex;
  margin: 0 auto;
  justify-content: center;
}
.keyboard__key {
  flex: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--key-bg-color);
  font-size: 1.8rem;
  width: 4rem;
  height: 5rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin: 0 .2rem;
  padding: 0 .1rem;
  border-radius: 1rem;
  box-shadow: 0 .5rem 0 rgba(0, 0, 0, 0.2);
  line-height: 1;

}
.keyboard__row {
  padding: .5rem 0;
}
.keyboard__key:focus,
.keyboard__key:active {
  outline: none;
}
.keyboard__key[data-type="functional"] {
  width: 8rem;
}
.keyboard__key[data-code="Space"] {
  width: 21.5rem;
}
.keyboard__key[data-code="ArrowLeft"],
.keyboard__key[data-code="ArrowRight"],
.keyboard__key[data-code="ArrowUp"],
.keyboard__key[data-code="ArrowDown"] {
  width: 4.6rem;
}
.keyboard__key[data-code="Backspace"] {
  width: 11rem;
}

.active {
  cursor: pointer;
  box-shadow: 0px 2px 0px rgba(0, 0, 0, 0.2);
  transform: translateY(3px);
  transition: all 0.1s ease-in-out;
  background-color: #808080;
}
.keyboard__key[data-code="Mic"],
.keyboard__key[data-code="Volume"],
.keyboard__key[data-code="Close"] {
  width: 4rem;

}

@media screen and (max-width: 500px) {
  html {
    font-size: 5px;
  }
  .keyboard {
    padding: .1rem;
    width: 100%;
  }
  .keyboard__key {
    font-size: 2.3rem;
  }
}
.open {
  position: fixed;
  z-index: 1;
  bottom: 2rem;
  left: 50%;
  transform: translateX(-50%);
}
